@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,500;0,600;0,700;0,800;0,900;1,100&display=swap);
:root {
  --button_color: #fd9800;
  --article_text_background: rgba(0, 0, 0, 0.582);
  --categorized_component_background: rgba(0, 0, 0, 0.13);
  --primary_font: "Poppins", sans-serif;
  --search_icon_light : url(data:image/png;base64,);
  --search_icon_black: url(data:image/png;base64,);
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: white;
  margin: 0;
  font-family: "Poppins", sans-serif;
  font-family: var(--primary_font);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: 0.7s background-color ease-in-out;
}

.parent_loader {
  text-align: center;
  width: 100%;
  height: 100vh;
  position: relative;
}

.loader_container {
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translateX(-50%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  max-width: 1450px;
  margin: 0 auto;
  position: relative;
}

.navigation {
  width: 100%;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 9;
}

.line_breaker {
  width: 100%;
  height: 1px;
  background-color: gray;
}

.navbar {
  background-color: #1b2838;
  border-bottom: 1px solid rgba(0, 0, 0, 0.308);
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 10;
  padding: 0.3rem 0;
}

.navbar .menu {
  width: 95%;
  margin: 0 auto;
  padding: 0.1rem 0;
  padding-top: 0.3rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbar .menu .realtime_date_section {
  display: flex;
  color: white;
}

.navbar .menu .realtime_date_section p {
  margin-right: 1rem;
  font-size: 14px;
}

.navbar .menu .right_menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbar .menu .right_menu .night_mode_section {
  margin-right: 0.5rem;
}

.navbar .menu .right_menu .social_media_section {
  display: flex;
}

.navbar .menu .right_menu .social_media_section img {
  width: 25px;
  margin: 0 0.5rem;
  cursor: pointer;
}

.navbar .menu .right_menu .search_section {
  margin: 0 0 0 1rem;
}

.subnav {
  border-bottom: 1px solid rgba(0, 0, 0, 0.459);
}

.subnav .subnav_container {
  width: 95%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.subnav .subnav_container .site_logo {
  margin: 0.75rem 0;
}

.subnav .subnav_container .site_logo .site_markup {
  display: flex;
  align-items: flex-end;
  text-decoration: none;
}

.subnav .subnav_container .site_logo .site_markup img {
  width: 150px;
}

.subnav .subnav_container .site_logo .site_markup h2 {
  margin-left: 0.3rem;
  font-size: 18px;
  color: red;
}

.subnav .subnav_container nav ul {
  display: flex;
  list-style: none;
}

.subnav .subnav_container nav ul li {
  padding: 0 1rem;
  transition: 0.3s ease-in-out;
}

.subnav .subnav_container nav ul li:hover {
  color: orange;
}

.subnav .subnav_container .mobile_access_right {
  display: none;
}

.search_container input[type="search"] {
  width: 30px;
  height: 30px;
  border-radius: 20px;
  font-size: 14px;
  border: none;
  outline: none;
  background-color: transparent;
  background-image: var(--search_icon_light);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-repeat: no-repeat;
  background-size: 30px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  color: transparent;
}

.search_container input[type="search"]:focus, .search_container input[type="search"]:hover {
  background-color: #FFFFFF;
  background-image: var(--search_icon_black);
  background-size: 30px;
}

.search_container input[type="search"]:focus {
  width: 250px;
  height: 30px;
  cursor: text;
  padding-left: 40px;
  padding-right: 10px;
  background-size: 30px;
  color: black;
}

.night_mode_switch {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
}

.night_mode_switch .night_container {
  margin-left: 0.5rem;
  width: 63px;
  height: 20px;
  border-radius: 30px;
  background-color: white;
  position: relative;
  cursor: pointer;
}

.night_mode_switch .night_container .toggler {
  position: absolute;
  width: 27px;
  height: 27px;
  border-radius: 50%;
  background-color: white;
  transition: 0.5s linear;
}

.night_mode_switch .night_container .light_toggle {
  border: 1px solid #ff9c00;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.night_mode_switch .night_container .dark_toggle {
  border: 1px solid black;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.night_mode_switch .night_container .night_mode_icon img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 17px;
}

.night_mode_switch .night_container .toggle_label {
  position: absolute;
  transform: translateY(-50%);
}

.night_mode_switch .night_container .toggle_label p {
  line-height: 12px;
  font-size: 10px;
  font-weight: 700;
  transition: 0.5s linear;
}

.night_mode_switch .night_container .label_left {
  top: 50%;
  left: 6px;
}

.night_mode_switch .night_container .label_left p {
  color: #222222;
}

.night_mode_switch .night_container .label_right {
  top: 50%;
  right: 6px;
}

.night_mode_switch .night_container .label_right p {
  color: white;
}

.night_mode_switch .dark_switch_container {
  background-color: #1b2838;
  border: 1px solid white;
  border-left: 0;
}

.night_mode_switch .light_switch_container {
  background-color: white;
}

.mobile_menu {
  width: 40%;
  transition: 3s ease-in-out;
  color: white;
  height: 100%;
  position: fixed;
  top: 0;
  right: -40%;
  z-index: 10;
  background-color: rgba(49, 49, 49, 0.945);
  overflow-x: hidden;
  text-align: center;
  transition: 0.4s ease-in-out;
}

.mobile_menu .mobile_menu_header {
  margin: 1rem;
  display: flex;
  justify-content: space-between;
}

.mobile_menu .mobile_menu_header .mobile_site_logo {
  display: flex;
  align-items: flex-end;
}

.mobile_menu .mobile_menu_header .mobile_site_logo h3 {
  margin-left: 0.2rem;
  color: red;
  font-size: 15px;
}

.mobile_menu .mobile_menu_list ul {
  margin-top: 4rem;
}

.mobile_menu .mobile_menu_list ul li {
  margin: 0.4rem 0;
  padding: 0.3rem 0;
}

.__link {
  text-decoration: none;
  padding: 0 0.2rem 0.1rem 0.2rem;
  color: inherit;
}

@media screen and (max-width: 950px) {
  .subnav .subnav_container nav ul li {
    padding: 0 0.5rem;
  }
}

@media screen and (max-width: 810px) {
  .subnav .subnav_container nav {
    display: none;
  }
  .subnav .subnav_container .mobile_access_right {
    display: block;
  }
  .subnav .subnav_container .mobile_access_right .mobile_nav .burger_menu .b_line {
    height: 4px;
    background-color: #272727;
    margin: 0.2rem 0;
    border-radius: 4px;
  }
  .subnav .subnav_container .mobile_access_right .mobile_nav .burger_menu .b_1 {
    width: 20px;
  }
  .subnav .subnav_container .mobile_access_right .mobile_nav .burger_menu .b_2 {
    width: 24px;
  }
  .subnav .subnav_container .mobile_access_right .mobile_nav .burger_menu .b_3 {
    width: 28px;
  }
}

@media screen and (max-width: 630px) {
  .navbar .menu .realtime_date_section {
    display: none;
  }
}
/*# sourceMappingURL=navbar.css.map */
.footer {
  width: 100%;
  background-image: linear-gradient(#d202ff, #9400c4);
  padding: 0.7rem 0;
  color: white;
}

.footer .footer_container {
  width: 95%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer .footer_container .footer_info img {
  width: 100px;
}

.footer .footer_container .footer_info p {
  font-size: 12px;
}

.footer .footer_container .advertise h1 {
  font-size: 17px;
  padding: 0.7rem 1rem;
  background-color: #5e5e5e;
}
/*# sourceMappingURL=footer.css.map */
.news_feed {
  width: 85%;
  margin: 2rem auto;
  display: flex;
  justify-content: space-between;
}

.news_details {
  width: 65%;
}

.read_more {
  width: 33%;
  background-color: rgba(0, 0, 0, 0.205);
}

.read_more .read_more_header {
  padding: 0.5rem 0.75rem;
  margin-bottom: 1rem;
  border-bottom: 2px solid rgba(128, 128, 128, 0.363);
}

.read_more .read_more_header h1 {
  font-size: 25px;
}

.headline_img img {
  width: 100%;
}

.category {
  margin-top: 2rem;
  display: inline-block;
  background-color: #bb0505;
  padding: 0.2rem 0.5rem;
  color: white;
}

.category .cat_list {
  color: white;
  text-decoration: none;
}

.f_post_details {
  display: flex;
  margin-top: 1rem;
  justify-content: space-between;
}

.f_title h1 {
  font-size: 1.5rem;
  margin: 1rem 0;
}

.f_description h3 {
  font-weight: normal;
  font-size: 17px;
  line-height: 32px;
}

.f_description * img {
  max-width: 100%;
  max-height: 400px;
  margin-left: 50%;
  object-fit: cover;
  transform: translateX(-50%);
}

.also_read {
  width: 85%;
  margin: 2rem auto 4rem auto;
}

.also_read .also_read_list ul {
  list-style: none;
}

.also_read .also_read_list ul li {
  margin: 0.5rem 0;
}

.link {
  text-decoration: none;
  color: inherit;
}

.also_link {
  color: inherit;
}

@media screen and (max-width: 1146px) {
  .news_feed {
    width: 90%;
  }
}

@media screen and (max-width: 1010px) {
  .news_feed {
    width: 95%;
  }
  .news_feed .news_details {
    width: 60%;
  }
  .news_feed .read_more {
    width: 38%;
  }
}

@media screen and (max-width: 720px) {
  .news_feed {
    display: block;
  }
  .news_feed .news_details {
    width: 100%;
  }
  .news_feed .read_more {
    width: 85%;
    margin: 0 auto;
  }
}

@media screen and (max-width: 575px) {
  .news_feed .news_details .f_description h3 {
    line-height: 30px;
  }
  .news_feed .read_more {
    width: 100%;
  }
}

@media screen and (max-width: 311px) {
  .news_feed .news_details .f_title h1 {
    font-size: 20px;
  }
  .news_feed .news_details .f_description {
    border: 1px solid red;
  }
  .news_feed .news_details .f_description h3 {
    font-size: 14px;
    line-height: 25px;
  }
}
/*# sourceMappingURL=news_details.css.map */
.read_more_section ul {
  padding: 0 0.75rem;
  list-style: none;
}

.read_more_section ul li .read_list {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.171);
}

.read_more_section ul li .read_list .read_news_img {
  width: 40%;
  height: 80px;
}

.read_more_section ul li .read_list .read_news_img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.read_more_section ul li .read_list .read_news_title {
  width: 58%;
}

.read_more_section ul li .read_list .read_news_title h3 {
  font-size: 13px;
  font-weight: 400;
}

.read_more_section ul li .read_list .read_news_title .read_news_details p {
  font-size: 10px;
  color: gray;
}

.read_more_section ul li .read_list:hover .read_news_title h3 {
  text-decoration: underline;
}

._link {
  color: inherit;
  color: inherit;
  text-decoration: none;
}

@media screen and (max-width: 860px) {
  .read_more_section ul li .read_list .read_news_img {
    height: 70px;
    width: 40%;
  }
  .read_more_section ul li .read_list .read_news_title h3 {
    font-size: 12px;
  }
  .read_more_section ul li .read_list:hover .read_news_title h3 {
    text-decoration: none;
  }
}

@media screen and (max-width: 720px) {
  .read_more_section ul li .read_list .read_news_img {
    height: 100px;
    width: 30%;
  }
  .read_more_section ul li .read_list .read_news_title {
    width: 65%;
  }
  .read_more_section ul li .read_list .read_news_title h3 {
    font-size: 15px;
  }
}

@media screen and (max-width: 455px) {
  .read_more_section ul li .read_list .read_news_img {
    width: 40%;
  }
  .read_more_section ul li .read_list .read_news_title {
    width: 55%;
  }
  .read_more_section ul li .read_list .read_news_title h3 {
    font-size: 13px;
  }
}

@media screen and (max-width: 400px) {
  .read_more_section ul li .read_list .read_news_img {
    height: 80px;
    width: 40%;
  }
  .read_more_section ul li .read_list .read_news_title {
    width: 55%;
  }
  .read_more_section ul li .read_list .read_news_title h3 {
    font-size: 13px;
  }
}
